@import '@mixins';

.mainShop {
    @include container;
    display: grid;
    grid-template-columns: 20% 80%;
    padding: 30px 20px 0;
    background-color: #ededed62;
    position: relative;
    border-radius: 5px;
}

@media (max-width: 768px) {
    .mainShop {
        display: block;
    }
}